$badge-pill-border-radius: 1rem;*/
table {
  page-break-inside: auto;
  /* This is the key */
}
table tr {
  page-break-inside: avoid;
  page-break-after: auto;
}
table thead {
  display: table-header-group;
}
table tfoot {
  display: table-footer-group;
}
table.table {
  margin: 0;
  width: 99.9%;
  border-collapse: collapse;
  font-size: 8.5pt;
  border-left: 0.1em solid #090e1e;
  border-right: 0.1em solid #090e1e;
  border-top: 0.1em solid #090e1e;
  border-bottom: 0.1em solid #090e1e;
}
table.table th {
  text-align: center;
  font-weight: bold;
}
table.table td {
  vertical-align: top;
}
table.table th, table.table td {
  padding: 4pt;
}
table.table th.border-0, table.table td.border-0 {
  border: 0 !important;
}
table.table th.border-start-0, table.table td.border-start-0 {
  border-left: 0 !important;
}
table.table th.border-end-0, table.table td.border-end-0 {
  border-right: 0 !important;
}
table.table th.border-end-1, table.table td.border-end-1 {
  border-right: 0.1em solid #090e1e !important;
}
table.table th.border-top-0, table.table td.border-top-0 {
  border-top: 0 !important;
}
table.table th.border-top-1, table.table td.border-top-1 {
  border-top: 0.1em solid #090e1e !important;
}
table.table th.border-bottom-0, table.table td.border-bottom-0 {
  border-bottom: 0 !important;
}
table.table th.border-bottom-1, table.table td.border-bottom-1 {
  border-bottom: 0.1em solid #090e1e !important;
}
table.table.table-bordered th, table.table.table-bordered td {
  border-left: 0.1em solid #090e1e;
  border-right: 0;
  border-top: 0.1em solid #090e1e;
  border-bottom: 0;
  border-collapse: collapse;
}
table.table.table-detail-view {
  width: 50%;
}
table.table.table-detail-view th {
  text-align: left;
}
table.table.table-borderless {
  border: none;
}
table.table.table-borderless tr, table.table.table-borderless td, table.table.table-borderless th {
  border: none;
}

.card {
  margin-top: 6pt;
  padding: 6pt 0;
}
.card .table th, .card .table td {
  padding: 5pt 0;
  border: none;
}
.card .table.table-grid-view th, .card .table.table-grid-view td {
  padding: 5pt;
  border: 1pt solid #000;
}
.card .table.table-grid-view thead tr th {
  text-align: center;
}

